import { Link } from 'gatsby'
import PropTypes from 'prop-types'
import React from 'react'

// styled
import {
  HeaderWrapper,
  Navigation,
  NavigationUl,
  NavigationLi,
  SiteTitleWrapper,
  LogoWrapper,
  Logo,
  SiteTitle,
  Description,
  SocialMediaWrapper,
  SocialMediaButton,
  SocialMediaText,
} from '../styling/header.style'

// social media icon
import GithubIcon from '../components/Icons/GithubIcon'
import FacebookIcon from '../components/Icons/FacebookIcon'
import TwitterIcon from '../components/Icons/TwitterIcon'
import LinkedInIcon from '../components/Icons/LinkedInIcon'

class Header extends React.Component {
  state = {
    colorMenu: `#e8e3e3`,
    bgLogo: `rgb(22,160,133)`,
  }

  componentDidMount() {
    document.addEventListener('scroll', this.changeColorMenu)
  }

  changeColorMenu = () => {
    this.setState((prevState, props) => {
      if (window.scrollY > 118)
        return {
          colorMenu: `#ffffff`,
        }
      else
        return {
          colorMenu: `#e8e3e3`,
        }
    })
  }

  componentWillUnmount() {
    document.removeEventListener('scroll', this.changeColorMenu)
  }

  render() {
    const { siteTitle, siteDescription } = this.props
    return (
      <div>
        <HeaderWrapper>
          <Navigation>
            <NavigationUl>
              <NavigationLi
                colorMenu={this.state.colorMenu}
                colorHover={`rgb(22, 160, 133)`}
              >
                <Link to={`/`} activeClassName="active">
                  Home
                </Link>
              </NavigationLi>
              <NavigationLi
                colorMenu={this.state.colorMenu}
                colorHover={`rgb(231, 76, 60)`}
              >
                <Link to={`/about`} activeClassName="active">
                  About
                </Link>
              </NavigationLi>
              <NavigationLi
                colorMenu={this.state.colorMenu}
                colorHover={`rgb(41, 128, 185)`}
              >
                <Link to={`/project`} activeClassName="active">
                  Project
                </Link>
              </NavigationLi>
              <NavigationLi
                colorMenu={this.state.colorMenu}
                colorHover={`#ffbd4c`}
              >
                <Link to={`/contact`} activeClassName="active">
                  Contact
                </Link>
              </NavigationLi>
            </NavigationUl>
          </Navigation>
          <SiteTitleWrapper>
            <LogoWrapper>
              <Logo bgLogo={this.state.bgLogo}>SK</Logo>
              <SiteTitle>{siteTitle}</SiteTitle>
            </LogoWrapper>
            <Description>{siteDescription}</Description>
          </SiteTitleWrapper>
          <SocialMediaWrapper>
            <SocialMediaButton
              type={`github`}
              href={'https://github.com/sgtkuncoro'}
              target={`_blank`}
            >
              <GithubIcon size={18} />
              <SocialMediaText> Github</SocialMediaText>
            </SocialMediaButton>
            <SocialMediaButton
              type={`twitter`}
              href={'https://twitter.com/sgtkuncoro'}
              target={`_blank`}
            >
              <TwitterIcon size={18} />
              <SocialMediaText> Twitter</SocialMediaText>
            </SocialMediaButton>
            <SocialMediaButton
              type={`linkedin`}
              href={'https://www.linkedin.com/in/sigit-kuncoro-98476314a/'}
              target={`_blank`}
            >
              <LinkedInIcon size={18} />
              <SocialMediaText> LinkedIn</SocialMediaText>
            </SocialMediaButton>
            <SocialMediaButton
              href={'https://www.facebook.com/sigit.kunc'}
              target={`_blank`}
            >
              <FacebookIcon size={18} />
              <SocialMediaText> Facebook</SocialMediaText>
            </SocialMediaButton>
          </SocialMediaWrapper>
        </HeaderWrapper>
      </div>
    )
  }
}

Header.propTypes = {
  siteTitle: PropTypes.string,
  siteDescription: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: '',
  siteDescription: '',
}

export default Header
